import {makeAutoObservable, observable} from "mobx";
import AuthKey from "../../../Model/AuthKey";
import LoaderPagination from "../../../Lib/LoaderPagination";
import Requests from "../../../Api/Requests";
import {Order} from "./component/OrderCard/OrderCard";

class OrdersStore {
    @observable
    Loader: LoaderPagination<Order> = new LoaderPagination<Order>(2, AuthKey.GetFromLocalStorage(), Requests.Orders); //   ,      ,    product,                   + 

    constructor() {
        makeAutoObservable(this);
    }
}

export default OrdersStore;