import React from "react";
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import styles from "./OrderHistoryCard.module.css"
import OrderItem from "../../../../../Model/Order/OrderItem";
import MealCategory from "../../../../../Model/Enum/MealCategory";
import {IStudent} from "../../../ParentProfile/Store/IStudent";
import IParent from "../../../../../Model/Interface/IParent";
import {getInitials} from "../../../../../Lib/Transormators";


export interface Order {
    id: number;
    order_items: OrderItem[];
    order_date: Date;
    date_ordered: Date;
    meal_category: MealCategory;
    student: IStudent;
}

export class OrderHistoryCard extends React.Component<{ order: Order, user: IParent }> {

    render() {
        let {order, user} = this.props;
        return (
            <Container className={styles.OrderHistoryContainer}>
                <Card className={styles.OrderHistoryCard}>
                    <Card.Body>
                        <Card.Title className={styles.Title}> {order.id}</Card.Title>
                        <Card.Text><h4 className={styles.Subtitle}>:</h4><p
                            className={styles.DescriptionText}>{order.date_ordered.toLocaleString()}</p></Card.Text>
                        <Card.Text><h4 className={styles.Subtitle}>: </h4><p
                            className={styles.DescriptionText}>{getInitials(user)}. </p></Card.Text>
                        <Card.Text><h4 className={styles.Subtitle}>: </h4><p
                            className={styles.DescriptionText}>{getInitials(order.student)} {order.student.grade} </p></Card.Text>
                        <Card.Text><h4 className={styles.Subtitle}> : </h4></Card.Text>
                        <Card.Text>
                            <h4 className={styles.Subtitle}>: </h4>
                            <div className={styles.CardText}>
                                <p className={styles.FoodItem}> </p>
                                <p className={styles.FoodItemCount}>1</p>
                                <p className={styles.FoodItemPrice}>50 </p>
                            </div>
                            <div className={styles.CardText}>
                                <p className={styles.FoodItem}></p>
                                <p className={styles.FoodItemCount}>1</p>
                                <p className={styles.FoodItemPrice}>30 </p>
                            </div>
                            <div className={styles.CardText}>
                                <p className={styles.FoodItem}> </p>
                                <p className={styles.FoodItemCount}>1</p>
                                <p className={styles.FoodItemPrice}>15 </p>
                            </div>
                        </Card.Text>
                        <Card.Text>
                            <h4 className={styles.Subtitle}>: </h4>
                            <div className={styles.CardText}>
                                <p className={styles.FoodItem}> </p>
                                <p className={styles.FoodItemCount}>1</p>
                                <p className={styles.FoodItemPrice}>50 </p>
                            </div>

                            <div className={styles.CardText}>
                                <p className={styles.FoodItem}></p>
                                <p className={styles.FoodItemCount}>1</p>
                                <p className={styles.FoodItemPrice}>15 </p>
                            </div>

                        </Card.Text>

                        <Card.Text>
                            <h4 className={styles.Subtitle}>: 195 </h4>
                        </Card.Text>
                    </Card.Body>
                </Card>
            </Container>
        )
    }
}